import React, { useState } from "react";
import { Button, Form, Card, Col, Row } from "react-bootstrap";
import * as Api from "../../../utils/api";

function UserEditForm({ user, setIsEditing, setUser }) {
  //useState name  .
  const [name, setName] = useState(user.name);
  //useState description  .
  const [description, setDescription] = useState(user.description);
  //useState imgUrl  .
  const [ imgUrl, setImgUrl ] = useState(user.imgUrl);

  const SubmitImg = async (e) => {
    e.preventDefault();
    const img = e.target.files[0];

    const res2 = await Api.imgpost(`users/${user._id}/uploads`, img);
    const newImgUrl = res2.data.imagePath;
    setImgUrl(newImgUrl);
}

  const handleSubmit = async (e) => {
    e.preventDefault();

    // "users/id"  PUT .
    const res = await Api.put(`users/${user._id}`, {
      name,
      description,
      imgUrl,
    });
    //   response data.
    const updatedUser = res.data;
    //    user .
    setUser(updatedUser);

    // isEditing false .
    setIsEditing(false);
  };

  return (
    <Card className="mb-2">
      <Card.Body>
          <Row className="justify-content-md-center">
            <Card.Img
              style={{ width: "10rem", height: "8rem", borderRadius: "50%" }}
              className="mb-3"
              src={imgUrl}
              alt="  "
            />
          </Row>
          <Form.Group encType='multipart/form-data' controlId="useProfileImg" className="mb-3">
            <Form.Control
              type="file"
              accept="image/*"
              onChange={SubmitImg}
            />
          </Form.Group>
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="useEditName" className="mb-3">
            <Form.Control
              type="text"
              placeholder=""
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="userEditEmail" className="mb-3">
            <Form.Control
              type="email"
              placeholder=""
              value={user.email}
              disabled
            />
          </Form.Group>

          <Form.Group controlId="userEditDescription">
            <Form.Control
              type="text"
              placeholder=", "
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            />
          </Form.Group>

          <Form.Group as={Row} className="mt-3 text-center">
            <Col sm={{ span: 20 }}>
              <Button variant="primary" type="submit" className="me-3">
                
              </Button>
              <Button variant="secondary" onClick={() => setIsEditing(false)}>
                
              </Button>
            </Col>
          </Form.Group>
        </Form>
      </Card.Body>
    </Card>
  );
}

export default UserEditForm;
