import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import * as Api from "../../../utils/api";


function AwardAddForm({ portfolioOwnerId, setAwards, setIsAdding, setIsVisibility }) {
  //useState name  .
  const [name, setName] = useState("");
  //useState organization  .
  const [organization, setOrganization] = useState("");
  //useState awardedDate  .

  const [ awardedDate, setAwardedDate ] = useState('');

  //useState Info  .
  const [ Info, setInfo ] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    // portfolioOwnerId userId  .
    const userId = portfolioOwnerId;

    // "award/create"  post.
    await Api.post(`${userId}/awards`, {
      userId,
      name,
      organization,
      awardedDate,
      Info,
    });

    // "awardlist/id"  get.
    const res = await Api.get(`${userId}/awards`);
    // awards response data .
    setAwards(res.data);
    // award   , isAdding false .
    setIsAdding(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="formBasicName">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=""
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="formBasicOrganization" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={organization}
          onChange={(e) => setOrganization(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlid="formBasicawardedDate" className="mt-3">
        <Form.Label> </Form.Label>

          <Form.Control
            type ="Date"
            value={awardedDate}
            onChange={(e)=>setAwardedDate(e.target.value)}
            />
        </Form.Group>

        <Form.Group controlId="formBasicInfo" className="mt-3">
          <Form.Label> ()</Form.Label>
          <Form.Control
            type="text"
            placeholder=""
            value={Info}
            onChange={(e) => setInfo(e.target.value)}
          />
      </Form.Group>


      <Form.Group as={Row} className="mt-3 text-center">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3" onClick={()=>setIsVisibility(true)}>
            
          </Button>
          <Button variant="secondary" onClick={() => {
            setIsAdding(false)
            setIsVisibility(true)}}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default AwardAddForm;

