import React, { useState, useEffect, useReducer, createContext } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import * as Api from "./utils/api";
import { loginReducer } from "./reducer";

import Header from "./components/common/Header";
import { LoginPage, SearchPortfolios, RegisterPage , MyPage, ProjectDetail} from "./pages";
import ProjectWrapper from "./pages/ProjectWrapper";
import ProfileWrapper from "./pages/ProfileWrapper";
import NotFoundPage from "./pages/NotFoundPage";
// import Educations from "./components/education/Educations";

export const UserStateContext = createContext(null);
export const DispatchContext = createContext(null);

function App() {
  // useReducer   userState  dispatch .
  const [userState, dispatch] = useReducer(loginReducer, {
    user: null,
  });

  //  fetchCurrentUser      .
  //    isFetchCompleted  true  .
  const [isFetchCompleted, setIsFetchCompleted] = useState(false);

  const fetchCurrentUser = async () => {
    try {
      //    ,     .
      const res = await Api.get("user/current");
      const currentUser = res.data;

      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: currentUser,
      });

      console.log("%c sessionStorage  .", "color: #d93d1a;");
    } catch {
      console.log("%c SessionStorage  .", "color: #d93d1a;");
    }
    // fetchCurrentUser  , isFetchCompleted  true 
    setIsFetchCompleted(true);
  };

  // useEffect  fetchCurrentUser  .
  useEffect(() => {
    fetchCurrentUser();
  }, []);

  if (!isFetchCompleted) {
    return "loading...";
  }

  return (
    <DispatchContext.Provider value={dispatch}>
      <UserStateContext.Provider value={userState}>
        <Router>
          <Header />
          <Routes>
          <Route path="/" exact element={<MyPage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/register" element={<RegisterPage />} />
            <Route path="/users/:userId" element={<MyPage />} />
            <Route path="/network" element={<SearchPortfolios />} />
            <Route path="*" element={<MyPage />} />
            <Route path= "/project" element={ <MyPage/>}/>
            <Route path= "/profile" element={<MyPage/>}/>
            <Route path="/projects/:id" element={<ProjectDetail />} />
            {/* <Route><NotFoundPage /></Route> */}
            
          </Routes>
        </Router>
      </UserStateContext.Provider>
    </DispatchContext.Provider>
  );
}

export default App;
