import React, { useState } from "react";
import { Button, Form, Col, Row, DropdownButton } from "react-bootstrap";
import * as Api from "../../../utils/api";
import DropdownItem from "react-bootstrap/esm/DropdownItem";


function EducationAddForm({ portfolioOwnerId, setEducations, setIsAdding , setIsVisibility }) {
  //useState school  .
  const [school, setSchool] = useState("");
  //useState major  .
  const [major, setMajor] = useState("");
  //useState educationLevel  .
  const [educationLevel, setEducationLevel] = useState(" ");
  //useState StartDate, endDate  
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');


  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();
  
    // portfolioOwnerId user_id  .
    const userId = portfolioOwnerId;

    // "education/write"  post.
  await Api.post(`${userId}/educations`, {
      userId ,
      school,
      major,
      educationLevel,
      startDate,
      endDate
    });

    // "educationlist/id"  get.
    const res = await Api.get(`${userId}/educations`);
    // awards response data .
    setEducations(res.data);
    // award   , isAdding false .
    setIsAdding(false);
  };

  return (
    <>
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="formBasicSchool">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=" ."
          value={school}
          onChange={(e) => setSchool(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="formBasicMajor" className="mt-3">
        <Form.Control
          type="text"
          placeholder="/ ."
          value={major}
          onChange={(e) => setMajor(e.target.value)}
        />
      </Form.Group>

      <br/>
      <Form.Group controlId="formBasicEducationLevel">
          <DropdownButton id="Educationlevel" title={educationLevel} onSelect={(eventKey)=>setEducationLevel(eventKey)}>
            <DropdownItem eventKey=""></DropdownItem>
            <DropdownItem eventKey=""></DropdownItem>
            <DropdownItem eventKey=" "> </DropdownItem>
            <DropdownItem eventKey=" "> </DropdownItem>
            <DropdownItem eventKey=" "> </DropdownItem>
          </DropdownButton>
      </Form.Group>
      <br/>      
      
      <Form.Group controlid="formBasicgetDate" className="mt-3">
          <Form.Label> </Form.Label>
          <Form.Control
              type ="Date"
              value={startDate}
              onChange={(e)=>setStartDate(e.target.value)}
          />
          <br/>
          <Form.Label> </Form.Label>
          <Form.Control
              type ="Date"
              value={endDate}
              onChange={(e)=>setEndDate(e.target.value)}
          />
        </Form.Group>

      <Form.Group as={Row} className="mt-3 text-center">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3" onClick={()=>setIsVisibility(true)}>
            
          </Button>
          <Button variant="secondary" onClick={() => {
            setIsAdding(false)
            setIsVisibility(true)}}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
    </>
  );
}

export default EducationAddForm;