import React, { useContext, useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Container, Col, Row,  Navbar, Nav } from "react-bootstrap";

import { UserStateContext } from "../App";
import * as Api from "../utils/api";
import User from "../components/features/user/User";
import ProfileWrapper from "./ProfileWrapper";
import ProjectWrapper from "./ProjectWrapper";

// import Educations from './Educations';
// import Awards from "./Awards";
// import Certificates from "./Certificates";
// import Projects from './Projects'
// import Project from './../components/features/project/Project';

function MyPage() {
  const navigate = useNavigate();
  const params = useParams();
  // useState   portfolioOwner  .
  const [portfolioOwner, setPortfolioOwner] = useState(null);
  // fetchPorfolioOwner    (isFetchCompleted true)   .
  //   , isFetchCompleted false "loading..." ,      .
  const [isFetchCompleted, setIsFetchCompleted] = useState(false);
  const userState = useContext(UserStateContext);
  const [selectedContent, setSelectedContent] = useState("project"); // 

  const fetchPorfolioOwner = async (ownerId) => {
    //  id  "/users/id"     .
    const res = await Api.get("users", ownerId);
    //   response data.
    const ownerData = res.data;
    // portfolioOwner    .
    setPortfolioOwner(ownerData);
    // fetchPorfolioOwner  , isFetchCompleted true .
    setIsFetchCompleted(true);
  };

  useEffect(() => {
    //   user null    ,   .
    if (!userState.user) {
      navigate("/login", { replace: true });
      return;
    }

    if (params.userId) {
      //   URL "/user<setSelectedC />d" ,  userId  id .
      const ownerId = params.userId;
      //   id fetchPorfolioOwner  .
      fetchPorfolioOwner(ownerId);
    } else {
      //  ,  URL "/" ,   user.id  id .
      const ownerId = userState.user._id;
      //   id fetchPorfolioOwner  .
      fetchPorfolioOwner(ownerId);
    }
  }, [params, userState, navigate]);

  if (!isFetchCompleted) {
    return "loading...";
  }

  if(!portfolioOwner) {
    return "loading..."
  }

  return (
    <Container fluid>
    <Navbar className="navbar mb-4" expand="xl"  sticky="top" bg= "light" >
  <Container>
    <Navbar.Brand placement="end" onClick={() => navigate("/main")}> {portfolioOwner.name}  </Navbar.Brand>
    <Navbar.Toggle aria-controls="navbar-nav" />
    <Navbar.Collapse id="navbar-nav">
      <Nav className="justify-content-md-end flex-grow-1 p-2 bd-highlight">
        <Nav.Link 
          onClick= {() => {
          setSelectedContent("project")
          navigate("/project") }
        } 
        id = "project"
        > 
           
          </Nav.Link> 
        
        
        <Nav.Link  
          onClick= {() => { 
          setSelectedContent("profile")
          navigate("/profile")} 
              }
              id = "profile"
              >
                 
          </Nav.Link>
        </Nav>
    </Navbar.Collapse>
    </Container>
</Navbar>
      <Row>
          <Col md="3" lg="3">
           <User
             portfolioOwnerId={portfolioOwner._id}
             isEditable={portfolioOwner._id === userState.user?._id}
           />
         </Col>

            
       {  selectedContent === "project" ? (
          <Col>
           <ProjectWrapper
             portfolioOwnerId={portfolioOwner._id}
             portfolioOwner= {portfolioOwner}
             userState={userState}
             navigate={navigate}
             />
          </Col>
         )  : (
        <Col>
        <ProfileWrapper
          portfolioOwnerId={portfolioOwner._id}
          portfolioOwner= {portfolioOwner}
          userState={userState}
          navigate={navigate}
           />
      </Col>
      
  )}
     </Row>
</Container>
     )
;}

export default MyPage;
